.search-bar {
    background-color: aquamarine;
    text-align: center;
    padding: 10px;
}

.search-input-container {
    background-color: #fff;
    margin-left: 650px;
    width: 250px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.search-input {
    background-color: #fff;
    border-color: transparent;
}

/* Responsive styles */
@media (max-width: 1200px) {
    .search-input-container {
        margin-left: 500px;
        width: 200px;
        margin-left: 650px;
    }
}

@media (max-width: 992px) {
    .search-input-container {
        margin-left: 350px;
        width: 200px;
        margin-left: 650px;
    }
}

@media (max-width: 768px) {
    .search-input-container {
        margin-left: 150px;
        width: 200px;
        margin-left: 650px;
    }
}

@media (max-width: 576px) {
    .search-input-container {
        margin-left: 0;
        width: 100%;
        justify-content: center;
        width: 200px;
        margin-left: 100px;
    }

    .search-input {
        width: 90%;
    }
}