/* Default styles */
.history-item {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin: 15px;
}

.history-item-info {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.time-accessed {
    margin-right: 35px;
}

.domain-logo {
    margin-right: 40px;
    height: 40px;
    width: 40px;
}

.title {
    font-size: 15px;
    font-weight: bold;
}

.delete-button {
    background-color: transparent;
    border-color: transparent;
}

/* Responsive styles for mobile screens */
@media (max-width: 576px) {
    .history-item {
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        margin: 5px;
    }

    .history-item-info {
        margin-top: 10px;
        justify-content: center;
    }

    .time-accessed {
        margin-right: 0;
        margin-bottom: 10px;
        font-size: 12px;
        margin-right: 15px;
    }

    .domain-logo {
        margin-right: 0;
        margin-bottom: 10px;
        height: 20px;
        width: 20px;
        margin-right: 10px;
    }

    .delete-button {
        margin-top: 10px;
    }
}
